.root:hover{color:#06c;text-shadow:rgba(0,51,102,.3) 0 -1px 0,#b3d9ff 0 2px 1px,#9df 0 0 5px,rgba(0,128,255,.6) 0 0 20px}.root{transition:background-color 250ms,color 250ms,border-color 250ms,margin-bottom 250ms,transform 250ms;transition-timing-function:linear}.root{position:absolute;background:linear-gradient(110deg, #eee 0%, #e3e3e3 10%, #fff 20.1%, #fff 25%, #f0f0f0 26%, #fff 28%, #ddd 55%, #eee 100%);border-radius:5px;border:1px solid #999;border-width:1px 1px 1px 1px;box-shadow:0px 2px 2px rgba(0,0,0,.5),0px 0px 5px rgba(0,0,0,.25),0px 0px 2px 4px rgba(0,0,0,.1)}.root{width:200px;height:50px;position:absolute;bottom:0;right:0;z-index:10;color:#2b2b2b;font-size:30px;text-align:center;line-height:50px;text-transform:uppercase;font-weight:600;cursor:pointer}.root:hover::after{content:"(^‿^)";position:absolute;top:0;left:0;width:200px;height:45px}.root::after{content:"(-‿-)";position:absolute;top:0;left:0;width:200px;height:45px}